import React, {Component} from 'react';
import Card from '../card/Card'
import './Cards.scss';

class AppCards extends Component {

    render(){
        const { shoppingCartData, addItems, handleImageError} = this.props
        const card = shoppingCartData.map((item, index) => {
            return <div key={index}> <Card eachShoppingCartData = {item}
            addItems={addItems}
            onImgError ={this.onImgError}
            index={index}
            handleImageError={handleImageError}/></div>
        })
        return(
            <div className="App-cards">
                {card}
            </div>
         );

    }
}

export default AppCards